.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    position: sticky;
    top: 0;
    z-index: 100;
    background-color: white;
}

.header__logo{
    height: 25px;
    width: 200px;
    object-fit: contain;
    margin-left: 20px;
}

.header__input{
    display: flex;
    align-items: center;
    width: 40%;
    border: 0.5px solid gray;
}

.header__icons{
    display: flex;
    align-items: center;
}

.header__input > input{
    flex: 1;
    border: none;
}

.header__inputbutton{
    width: 50px;
    background-color: #fafafa;
    border-left: 1px solid lightgray;
    color: gray;
}

.header__icon{
    margin-right:8px;
}